.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  background-image: linear-gradient(147deg, #282c34 0%, #04619f 74%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.content-wrap {
  flex: 1;
}